import {useContext} from 'react';
import ModalContext from './HDSModalContextProvider.js';

export default function useModal() {
  const ctx = useContext(ModalContext);
  if (!ctx) {
    throw new Error(
      'The `useModal` hook must be called from a descendent of `HDSModalContextProvider`'
    );
  }
  return {
    addModal: ctx.addModal,
  };
}
